body {
  background-image: url("backgroundCat.296caccb.jpg");
  background-repeat: no-repeat;
  background-size: cover;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.breed-select {
  max-width: 300px;
  width: 100%;
  height: 30px;
  transition: transform 250ms var(--timing-function);
  background-color: #fbedfa;
  border-radius: 4px;
  margin: 20px auto 20px 150px;
  overflow: auto;
  transform: translate(-50%, -50%);
  box-shadow: 0 1px 1px rgba(255, 3, 3, .14), 0 1px 3px rgba(176, 2, 2, .57), 0 2px 1px rgba(176, 44, 44, .647);
}

.breed-select > option {
  color: #891181;
}

.is-hidden {
  display: none;
}

.cat-info {
  text-align: center;
  max-width: 800px;
  height: auto;
  background-color: rgba(237, 139, 237, .106);
  border: 1px solid #840384;
  border-radius: 10px;
  gap: 10px;
  padding: 2%;
  display: flex;
  box-shadow: 0 1px 6px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 2px 1px rgba(46, 47, 66, .08);
}

.cat-img {
  border: 1px solid #840384;
  border-radius: 10px;
  overflow: hidden;
}

.cat-desk {
  width: 500px;
  background-image: radial-gradient(circle at 50% -20.71%, #cad7f9 0, #d0d6f9 6.25%, #d5d4f7 12.5%, #dbd3f5 18.75%, #e0d2f3 25%, #e5d0f0 31.25%, #eacfed 37.5%, #eecee9 43.75%, #f2cde5 50%, #f5cce1 56.25%, #f8ccdc 62.5%, #facbd8 68.75%, #fbcbd3 75%, #fccccf 81.25%, #fccccb 87.5%, #fcccc7 93.75%, #fbcdc3 100%);
  border: 1px solid #840384;
  border-radius: 10px;
  margin-left: 30px;
  padding: 10px 20px;
}

.cat-desk > h2, h3 {
  color: rgba(174, 18, 18, .973);
}

.cat-link {
  color: #891181;
  text-decoration: none;
}

.cat-link:visited {
  color: rgba(174, 18, 18, .973);
}

.loader {
  letter-spacing: .02em;
  color: #891181;
  font-size: 14px;
  font-weight: 700;
  line-height: 1.11;
}

.css-loader {
  width: 100px;
  height: 100px;
  position: relative;
  top: 30%;
  left: 20%;
}

.css-loader:before, .css-loader:after {
  content: "";
  border-radius: 50%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  box-shadow: inset 0 0 10px 2px rgba(0, 0, 0, .3);
}

.css-loader:after {
  animation: rotate 2s linear infinite;
  box-shadow: inset 0 2px #891181;
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

